.App {
  width: 100%;
  height: 100%;
  text-align: center;
}

.bounds-container {
  width: 100%;
  height: 100%;
}

.bounds-container .line {
  position: fixed;
  z-index: 1;
  background: #00aeef;
}

.bounds-container .line.top {
  top: 0;
  left: 0;
  width: 100%;
  height: 30px;
}

.bounds-container .line.bottom {
  bottom: 0;
  left: 0;
  width: 100%;
  height: 30px;
}

.bounds-container .line.left {
  top: 0;
  width: 30px;
  height: 100%;
  left: 0;
}

.bounds-container .line.right {
  top: 0;
  right: 0;
  width: 30px;
  height: 100%;
}

.dark-mode {
  background: #2e3440;
  color: #eceff4;
}

.dark-mode .exp-title,
.dark-mode .exp-header {
  color: #e5e9f0;
}

.dark-mode nav ul li a,
.dark-mode .card .card-body {
  background-color: #434c5e;
  color: #eceff4;
}

.dark-mode .card .card-body .tech-stack {
  color: #d8dee9;
}

.dark-mode .card .card-body a {
  color: #88c0d0;
}

@media (max-width: 600px) {
  .bounds-container .line.top,
  .bounds-container .line.bottom {
    height: 15px;
  }
}

@media (max-width: 600px) {
  .bounds-container .line.left,
  .bounds-container .line.right {
    width: 15px;
  }
}



