.about-wrapper {
  margin: auto;
  padding: 15vh 0 10vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
  width: 50vw;
}

.about-container {
  display: grid;
  grid-template-columns: minmax(50px, 5%) 1fr;
}

.about-profiles {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: auto;
      align-items: auto;
  -ms-flex-line-pack: center;
      align-content: center;
}

.about-profiles a {
  margin: 10px;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  color: inherit;
  text-decoration: none;
  -webkit-transition: all 0.2s ease 0s;
  -o-transition: all 0.2s ease 0s;
  transition: all 0.2s ease 0s;
}

.about-profiles a:hover {
  color: #00aeef;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

.about-profiles a:active {
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9);
}

.profile-pulse {
  border: 1px #cd39eb solid;
  -webkit-animation: pulse 1.5s infinite;
          animation: pulse 1.5s infinite;
}

.about-desc {
  margin-left: 20px;
  line-height: 1.8;
  text-align: left;
}

.about-container .about-desc #get-in-touch {
  color: #cd39eb;
  text-decoration: underline;
  cursor: pointer;
}

.experience-container {
  margin-top: 25px;
  text-align: left;
}

.experience-container .exp-item {
  position: relative;
  padding: 0 30px 20px 30px;
}

.experience-container .exp-item .exp-title {
  font-weight: bold;
}

.experience-container .exp-item .exp-title em {
  font-weight: 300;
}

.experience-container .exp-item .exp-notes {
  font-size: 14px;
}

.experience-container .exp-item .exp-timeframe {
  position: relative;
  top: -10px;
  margin: 0 0 5px 0;
  display: inline-block;
  padding: 0 5px;
  height: 20px;
  line-height: 18px;
  font-weight: 500;
  font-size: 11px;
  color: #cd39eb;
  border: 1px solid #cd39eb;
}

.experience-container .work-section .exp-item:not(:last-child)::before {
  content: '';
  position: absolute;
  left: 0;
  top: 3px;
  width: 1px;
  height: 100%;
  background: #cd39eb;
}

.experience-container .work-section .exp-item .exp-timeframe:not(:last-child)::before {
  content: '';
  width: 29px;
  height: 1px;
  position: absolute;
  left: -30px;
  top: 9px;
  background: #cd39eb;
}

@media (max-width: 1120px) {
  .about-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: auto;
        align-items: auto;
    -ms-flex-line-pack: center;
        align-content: center;
  }
  .about-profiles {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: auto;
        align-items: auto;
    -ms-flex-line-pack: start;
        align-content: flex-start;
  }
}

@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(205, 	57, 	235, 0.4);
  }
  60% {
    -webkit-box-shadow: 0 0 0 15px rgba(255, 0, 0, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(255, 0, 0, 0);
  }
}

@keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(205, 	57, 	235, 0.4);
  }
  60% {
    -webkit-box-shadow: 0 0 0 15px rgba(255, 0, 0, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(255, 0, 0, 0);
  }
}



