nav {
  margin-top: 50px;
  margin-left: 55px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: fixed;
  z-index: 1;
}

nav ul {
  padding: 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  list-style: none;
}

nav ul li {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  margin: 10px;
}

nav ul li a {
  padding: 10px 30px;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  text-decoration: none;
  color: black;
  background: #fff;
  border: 1px solid #4c566a;
}

nav ul li .dark-mode-toggle {
  cursor: pointer;
}

nav ul li .dark-mode-toggle:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: all 0.2s ease 0s;
  -o-transition: all 0.2s ease 0s;
  transition: all 0.2s ease 0s;
  cursor: pointer;
}

nav ul li .dark-mode-toggle:active {
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9);
}


nav ul li a:hover {
  color: #eceff4;
  background: #cd39eb;
  border: 1px solid #cd39eb;
}

.selected {
  color: #eceff4;
  background: #cd39eb;
  border-color: #cd39eb;
}

@media (max-width: 1000px) {
  nav {
    margin: 5% auto;
    left: 0;
    right: 0;
  }
}



